<template>
  <div>
    <div class="topbar-logo topbar-btn">
      <!--<a href="/"><img src="../../static/i/logo.png" style="width:42px;margin-top: 5px;"></a>-->
    </div>
    <div class="topbar-title topbar-btn">
      <router-link to="/home">趣回租后台管理系统</router-link>
    </div>
    <div class="topbar-account topbar-btn">
      <el-dropdown trigger="click">
        <el-button type="text" class="el-dropdown-link userinfo-inner"><i class="iconfont icon-headUser el-icon--left"></i>{{sysUserName}}<i class="el-icon-arrow-down el-icon--right"></i></el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item><i class="iconfont icon-modify el-icon--left"></i>修改密码</el-dropdown-item>
          <el-dropdown-item @click.native="logout"><i class="iconfont icon-out el-icon--left"></i>退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        sysUserName: ''
      }
    },
    mounted () {
      var userSession = this.getCookie('session')
      if (userSession) {
        this.sysUserName = userSession
      }
    },
    methods: {
      logout () {
        this.$confirm('确认退出吗？', '提示', {}).then(() => {
          this.$fetch('/system/loginOut')
            .then(res => {
              if (res.status === 200) {
                this.delCookie('session')
                this.$router.push({path: '/login', query: {redirect: this.$router.currentRoute.fullPath}})
              } else {
                this.$message({
                  message: res.msg,
                  type: 'warning'
                })
              }
            })
        }).catch(() => {
        })
      }
    }
  }
</script>






